<template>
  <div id="homepage">
    <!-- Connect Wallet -->
    <span class="white-text">Step 1:</span><br/><br/>
    <v-btn class="ma-2" outlined x-large dark color="indigo" @click="pairWallet">
      {{connect_wallet_button}}
    </v-btn>
    <div id="heading">
      Account Id: <span class="bold">{{accountId}}</span>
    </div>

    <br/><br/><br/><br/>
    <!-- Authenticate -->
    <span class="white-text">Step 2:</span><br/><br/>
    <v-btn class="ma-2" outlined x-large dark color="indigo" @click="authenticate">
      Authenticate
    </v-btn>
    <div id="heading">
      Status: <span class="bold">{{status}}</span>
    </div>

  </div>
</template>

<script>
  import { HashConnect } from 'hashconnect';
  import axios from 'axios';

  export default {
    name: 'HomePage',
    data: () => {
      return {
        connect_wallet_button: "Connect Wallet",
        paired:false,
        accountId: "---",
        status: "---",
        topic: "",
        serverUrl: "https://api.hashnft.app",
        // serverUrl: "http://localhost:3000",
        hashconnect: new HashConnect()
      }
    },
    mounted: () => {
      console.log("XXXXX")
    },
    methods: {
      // Pair wallet
      async pairWallet() {
        console.log("PAIRING...")
        this.accountId = "---"
        this.status = "---"
        let appMetadata = {
            name: "Hash Collab",
            description: "Hash Collab - Discord Role Verifier",
            icon: "https://absolute.url/to/icon.png",
            url: "https://collab.hashnft.app/"
        }

        if(this.paired){
          await this.hashconnect.disconnect(this.topic)
          this.connect_wallet_button= "Connect Wallet"
          return
        }

        let initData = await this.hashconnect.init(appMetadata, "mainnet", false);

        this.hashconnect.foundExtensionEvent.once((walletMetadata) => {
            console.log(walletMetadata)
            this.hashconnect.connectToLocalWallet(initData.pairingString, walletMetadata);
        })

        this.hashconnect.pairingEvent.once((pairingData) => {
            console.log('wallet paired xxx')
            console.log(pairingData)
            this.paired = true
            this.accountId = pairingData.accountIds[0];
            this.topic = pairingData.topic;
            this.connect_wallet_button = "Disconnect Wallet"
        })
      },
      // Authenticate
      async authenticate() {
        if (this.accountId == '---') {
          alert("Please connect your wallet first.")
          return
        }
        console.log("Authenticating...")
        const hcData = JSON.parse(window.localStorage.getItem('hashconnectData'))
        const urlParams = new URLSearchParams(window.location.search);
        const code = urlParams.get('code')
        console.log("hcdata", hcData)

        let res = await axios({
            method: 'POST',
            url: `${this.serverUrl}/get-auth-data`,
            headers: {
              'Content-Type': 'application/json'
            },
            data: {
              accountId: this.accountId,
              uuid: code,
              topic: this.topic
            },
        })
        
        const authData = res.data
        const payload = authData.payload
        console.log('payload', payload)

        console.log('server sig', authData.serverSignature)
        const serverSigAsBuffer = Buffer.from(Object.values(authData.serverSignature))

        console.log('topic', payload.data.topic, this.topic)
        console.log('acc', payload.data.accountId, this.accountId)

        const auth = await this.hashconnect.authenticate(
          payload.data.topic,
          payload.data.accountId,
          authData.serverSigningAccount,
          serverSigAsBuffer,
          payload
        )

        console.log('auth', auth)

        if (auth.success) {
          res = await axios({
            method: 'POST',
            url: `${this.serverUrl}/authenticate`,
            headers: {
              'Content-Type': 'application/json'
            },
            data: {
              signingAccount: payload.data.accountId,
              payload: auth
            },
          })
          console.log(res.data)
          this.status = res.data.status
        } else {
          this.status = "---"
          alert("Authentication rejected!")
        }

      }
    },
    setup() {
      
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  #homepage {
    margin-top: 200px;
    font-family: monospace, monospace;
  }

  #heading {
    color: white;
    font-size: x-large;
    margin-top: 30px;
  }

  .bold {
    font-weight: 500;
  }

  .white-text {
    color: white;
  }

</style>
